          flex-direction: column;
          align-items: center;
          &:last-child {
            margin-right: 0;
          }
          img {
            width: 215px;
            height: 215px;
          }
          h3 {
            margin: 45px 0 20px 0;
            font-size: 20px;
            font-weight: 500;
            color: #666;
          }
          p {
            line-height: 24px;
            font-size: 14px;
            font-weight: 400;
            color: #999;
          }
        }
      }
    }
    .fix {
      position: relative;
      width: 100%;
      height: 354px;
      background: url("../../assets/fix.jpg") no-repeat top fixed;
      background-size: cover;
      .fix-wrapper {
        padding-top: 105px;
        text-align: center;
        color: #fff;
        .title {
          margin-bottom: 25px;
          font-size: 32px;
          font-weight: 400;
        }
        .text {
          margin-bottom: 35px;
          font-size: 16px;
        }
        .link {
          display: block;
          margin: auto;
          width: 250px;
          height: 45px;
          line-height: 45px;
          color: #fff;
          background: #F58A36;
          border-radius: 5px;
          &:hover {
            background: #EA5B24;
          }
        }
      }
    }
  }

</style>



